import styled from '@emotion/styled'
import { ModalTypes } from '../Types';
import { Div } from 'Elements';

export const Footer: ModalTypes['Parts']['Footer'] = (props) => {
  const { children, ...rest } = props;
  return (
    <FooterWrapper {...rest}>
      {children}
    </FooterWrapper>
  )
}

const FooterWrapper = styled(Div)<ModalTypes['Props']['Footer']>`
  padding          : 16px;
  background-color : ${({ b }) => b ?? '#fff1f1'};
  border-top       : 1px solid #dee2e6;
  display          : flex;
  justify-content  : flex-end;
`